body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo {
  height:150px;
}

.subtitle {
  font-style: italic;
  font-size: x-large;
  color: coral;
}
.featuredHouse {
  border-radius: 2px;
  background-color: rgb(255, 190, 166);
}
.price {
  font-size: larger;
  color: coral;
}

.display-item {
  padding: 0 1.5em;
  margin: 0 auto;
}

.image {
  width: 100%;
  height: auto;
  max-width: 500px;
  max-height: 600px;
}

tr {
    cursor: pointer;
  }
  
